import React,  {Component}  from "react";
import TrackVisibility from 'react-on-screen';
import {render} from 'react-dom'
import Techstack from "./Techstack";
import Example from './index22'



import {
  Image,
  Technologies,
  Tech,
  TechImg,
  TechName,
  ContactWrapper,
} from "././AboutElements";

export const stackList = [
  {
    img: "https://raw.githubusercontent.com/Kooper-kosh/personal-portfolio/main/logo/android-studio.png",
    name: "Android Studio",
  },
  {
    img: "https://raw.githubusercontent.com/Kooper-kosh/personal-portfolio/main/logo/visual-studio-code.png",
    name: "Visual Studio Code",
  },
  {
    img: "https://raw.githubusercontent.com/Kooper-kosh/personal-portfolio/main/logo/visual-studio.png",
    name: "Visual Studio 2019",
  },
  {
    img: "https://raw.githubusercontent.com/Kooper-kosh/personal-portfolio/main/logo/javaeclipse.png",
    name: "Java Eclipse",
  },
  {
    img: "https://raw.githubusercontent.com/Kooper-kosh/personal-portfolio/main/logo/html.jpg",
    name: "HTML",
  },
  {
    img: "https://raw.githubusercontent.com/Kooper-kosh/personal-portfolio/main/logo/netbeans.png",
    name: "Netbeans",
  },
  {
    img: "https://raw.githubusercontent.com/Kooper-kosh/personal-portfolio/main/logo/blender.png",
    name: "Netbeans",
  },
  {
    img: "https://raw.githubusercontent.com/gurupawar/website/main/src/Assets/skill/responsive.svg",
    name: "Responsive",
  },
];

function About() {
  return (
    <ContactWrapper id="about">
      <TrackVisibility>
              {({ isVisible }) =>
               <div className={isVisible ? "animate__animated animate__bounce" : ""}>
      <div className="skill-bx">
    
          <Image
            src="https://avatars.githubusercontent.com/u/76100593?v=4"
            alt="man-svgrepo"
          />
           <center>
      
     <br></br>
      <div className="intro3">
      <Example
      text="Hello! Katrina Sal Susara is my name. I will turn 23 on September 11 of this year. I am a new graduate student in the BSIT program at Pamantasan ng Lungsod ng Valenzuela in Maysan, Valenzuela.

      I intend to apply for a QA tester or engineer position. I am knowledgeable in manual testing and am familiar with several testing techniques, including regression, smoke, performance, unit, and others. I'm still working on improving my automation testing skills by using Selenium and Java Eclipse. 
      
      I have not received any failing or incomplete grades during my college years. By the 2020–2021 academic year, I was on the dean's list. For our capstone project, Development and Evaluation of Chekka: Android-based Exam Checker for Veinte Reales National High School, I served as both the main programmer and the representative. It is a mobile phone that can check and evaluate bubble exam sheets. My teammate was given the opportunity to participate in and go to our department's 2nd CEIT Colloquium 2022, where we could show our capstone project and get recognized as one of the best researchers in our department. You can view our thesis or capstone project in the projects section. I'm torn between becoming a mobile developer, software developer, database administrator, or quality assurance tester for my profession. I feel comfortable using Android Studio and SQLite because our project is a mobile application, but I know in my mind that's not enough to follow that career. In fact, it was the first time I had ever created a mobile application.
      
      From my very first year of college, I have enjoyed developing software programs, but I do not have the motivation to do so, particularly with regard to website development. Despite knowing the fundamentals and syntax, I do not enjoy doing that. In becoming a database administrator.  I also enjoy and am proficient at creating and normalizing databases however I am uncertain of how to begin my career in this field. That is why  I choose to become a quality assurance tester because I find it challenging, enjoyable, and especially interesting in both manual and automated testing. i still need to code, be detailed, and apply critical thinking at all times. 
    "
      length={700}
      tag="a"
      className = "intro4"
      ellipsis="..."
      enabled>

        </Example>
        </div>
   
          </center>
          <br></br>
    

        </div>
        <br></br>
        <br></br>
     
          <div className="skill">
          <center><h2>Tools and Technologies</h2> </center>
       
          <br></br>
        <Techstack />
        </div>
      </div>
    
    }
          </TrackVisibility>
    </ContactWrapper>
  
  );

}

export default About;
