import { useState, useEffect } from "react";
import { Container, Row, Col } from "react-bootstrap";
import headerImg from "../assets/img/katdp-transformed.png";
import { ArrowRightCircle } from 'react-bootstrap-icons';
import 'animate.css';
import TrackVisibility from 'react-on-screen';
import React from "react";
import {
  AiFillGithub,
  AiOutlineTwitter,
  AiFillInstagram,
  AiFillLinkedin,
  AiFillFacebook,
} from "react-icons/ai";
export const Banner = () => {
  const [loopNum, setLoopNum] = useState(0);
  const [isDeleting, setIsDeleting] = useState(false);
  const [text, setText] = useState('');
  const [delta, setDelta] = useState(50 - Math.random() * 50);
  const [index, setIndex] = useState(1);
  const toRotate = [ "welcome to my portfolio"];
  const period = 2000;

  useEffect(() => {
    let ticker = setInterval(() => {
      tick();
    }, delta);

    return () => { clearInterval(ticker) };
  }, [text])

  const tick = () => {
    let i = loopNum % toRotate.length;
    let fullText = toRotate[i];
    let updatedText = isDeleting ? fullText.substring(0, text.length - 1) : fullText.substring(0, text.length + 1);

    setText(updatedText);

    if (isDeleting) {
      setDelta(prevDelta => prevDelta / 2);
    }

    if (!isDeleting && updatedText === fullText) {
      setIsDeleting(true);
      setIndex(prevIndex => prevIndex - 1);
      setDelta(period);
    } else if (isDeleting && updatedText === '') {
      setIsDeleting(false);
      setLoopNum(loopNum + 1);
      setIndex(1);
      setDelta(200);
    } else {
      setIndex(prevIndex => prevIndex + 1);
    }
  }

  return (
    <section className="banner" id="home">
      <Container>
        <Row className="aligh-items-center">
          <Col xs={20} md={30} xl={0}>
            <TrackVisibility>
              {({ isVisible }) =>
              <center>
              <div className={isVisible ? "animate__animated animate__zoomIn" : ""}>
             
                <h1>  {`Hi! I'm Kat.`} <br></br><span className="txt-rotate" dataPeriod="200" ><span className="wrap">{text}</span></span></h1>
                <br></br>
              
                  <p><div className="intro1">My skills, certificates, projects, resume, and information about me are all available here. You can reach me at the following social media links:</div>     </p>
                
          
                  <br></br>
         
                  <ul className="home-about-social-links">
                        <li className="social-icons">
                          <a
                            href="https://github.com/Kooper-kosh"
                            target="_blank"
                            rel="noreferrer"
                            className="icon-colour  home-social-icons"
                          >
                            <AiFillGithub />
                          </a>
                        </li>
                        <li className="social-icons">
                          <a
                            href="https://www.facebook.com/kaaaat.susara"
                            target="_blank"
                            rel="noreferrer"
                            className="icon-colour  home-social-icons"
                          >
                            <AiFillFacebook />
                          </a>
                        </li>
                        <li className="social-icons">
                          <a
                            href="https://www.linkedin.com/in/katrina-susara-a69234243/"
                            target="_blank"
                            rel="noreferrer"
                            className="icon-colour  home-social-icons"
                          >
                            <AiFillLinkedin />
                          </a>
                        </li>
                        <li className="social-icons">
                          <a
                            href="https://www.instagram.com/katsuuue_/"
                            target="_blank"
                            rel="noreferrer"
                            className="icon-colour home-social-icons"
                          >
                            <AiFillInstagram />
                          </a>
                        </li>
                      </ul>
                      <br></br>
          
                    
                      <h10><div className="email">susarakatrinasal@gmail.com</div></h10>
                     
                      <h10><div className="email">0939-763-0572</div></h10>
                   </div>
             
              </center>}
            </TrackVisibility>
          </Col>
        
        </Row>
      </Container>
    </section>
  )
}
